.main {
  z-index: 2;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 99px 99px 0 0;
  width: 1200px;
  padding-bottom: 50px;
  position: absolute;
  top: 150px;
  left: 50%;
  transform: translateX(-50%);
}

h1, h2, h3, h4, h5 {
  margin: 0;
  padding: 0;
}

:root {
  font-family: Poppins;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.background {
  z-index: 1;
  background-image: url("https://wallpapercave.com/wp/3a0T946.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.main-list {
