  text-align: center;
  border: 1px solid #d3d3d3;
  border-radius: 25px;
  flex-direction: column;
  gap: 10px;
  width: 500px;
  padding: 15px;
  display: flex;
}

textarea {
  border-radius: 8px;
  width: 100%;
  font-size: 25px;
}

textarea:focus {
  outline: none;
}

.main-postfield button {
  cursor: pointer;
  font-family: inherit;
  font-size: 17px;
  font-weight: 800;
  font-size: inherit;
  color: #f8f8ff;
  background: #6f6;
  border: none;
  border-radius: 500px;
  outline: none;
  width: 100%;
  margin: 0;
  padding: .9rem 2rem;
  display: block;
  position: relative;
  overflow: hidden;
}

.main-postfield button span {
  z-index: 10;
  transition: color .4s;
