}

.main-postfield button:before, .main-postfield button:after {
  z-index: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.main-postfield button:before {
  content: "";
  background: #000;
  width: 120%;
  transition: transform .4s cubic-bezier(.3, 1, .8, 1);
  left: -10%;
  transform: skew(30deg);
}

.main-postfield button:hover:before {
  transform: translate3d(100%, 0, 0);
}

